import React from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';

const BankBranchCode = React.memo(props => {
  const { handleChange, bankBranchCode } = props;
  return (
    <TextValidator
      required
      name="bankBranchCode"
      id="bankBranchCode"
      onChange={handleChange}
      value={bankBranchCode}
      label="Bank Branch Code"
      helperText=""
      placeholder="000000"
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[0-9]*$',
        'isNumber',
        'maxStringLength: 6',
      ]}
      errorMessages={[
        '',
        '',
        '',
        ' 6 ',
      ]}
      margin="dense"
      variant="outlined"
    />
  );
});

export default BankBranchCode;
